import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhAuth, VhQuery, VhQuerySales } from 'ionic-vhframeworks';
import { NzMessageService } from 'ng-zorro-antd/message';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { FunctionService } from 'src/app/sales/services/function.service';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-temporary-sales-invoice',
  templateUrl: './temporary-sales-invoice.component.html',
  styleUrls: ['./temporary-sales-invoice.component.scss']
})
export class TemporarySalesInvoiceComponent implements OnInit {

  public date: Date[] = [new Date(), new Date()];
  public searchValue: string = ''
  public invoiceShow: any[] = []
  public invoices: any[];
  invoiceFilter = []
  tableHeight: string;
  status: any;
  totalValInvoice: number = 0;
  dataRestore: any = this.router.getCurrentNavigation().extras.state
  customers = this.vhQuerySales.getlocalCustomers().filter(item => item.type != 3);
  constructor(
    private vhQuery: VhQuery,
    private router: Router,
    private vhQuerySales: VhQuerySales,
    public vhAlgorithm: VhAlgorithm,
    private vhComponent: VhComponent,
    private languageService: LanguageService,
    public vhAuth: VhAuth,
    private cdRef: ChangeDetectorRef,
    private message: NzMessageService,
    private fncService: FunctionService
  ) {
    this.date[0].setHours(0, 0, 0, 0)
    this.date[1].setHours(23, 59, 59, 59)
  }

  ngOnInit() {
    

    if (this.dataRestore) {
      this.searchValue = this.dataRestore.searchValue
      this.filterType = this.dataRestore.filterType
      this.date = [new Date(this.dataRestore.startTime), new Date(this.dataRestore.endTime)];

      this.invoices = this.dataRestore.invoice
      this.sortFilter(this.filterType)

      if (this.dataRestore.reload) this.getInvoices()
    }
    else { this.filterType = 'all'; }

    //this.vhComponent.showLoading("", "transparent-loading", null, 0, false).then(() => this.getInvoices())
  } 
  /**
   * get danh sách hóa đơn bán hàng từ DB
   * @example this.getInvoiceToday(1)
   */
  getInvoices() {
    if (this.vhAuth.checkMyPermission("sell_enable_view_bill"))
      this.vhComponent.showLoading("", "transparent-loading", null, 0, false).then(() => {
        this.vhQuerySales.getBills_byFields( { date: { $gte: new Date(this.date[0].setHours(0, 0, 0)), $lte: new Date(this.date[1].setHours(23, 59, 59, 59)) }, bill_type: { $in: [5] }, id_branch: { $eq: this.vhQuerySales.getDefaultBranch()._id }, id_shop: { $eq: 'local' } }, {})
          .then((invoice: any[]) => {
            console.log(invoice)
            this.invoices = invoice.map(item => {
              return { ...item, partner_name: this.renderPartner(item.id_customer), payment_name: this.renderPayment(item.payment_type, item.id_wallet),   name_employee: this.vhAuth.getlocalEmployee(item.id_employee).name }
            })
            this.sortFilter(this.filterType)
            this.status = "Not found"
            this.vhComponent.hideLoading(0);
          }, err => {
            this.vhComponent.hideLoading(0);
          })
      })
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
  
  /**
   * trả về tên kh của bill
   * @param id_partner 
   * @returns string
   * @example let customer_name = this.renderPartner('DNIKWUJHNBDUYWGU') 
   */
  renderPartner(id_partner: string) {
    let customer: string = this.languageService.translate(this.vhQuerySales.getlocalCustomer(id_partner).name)

    return customer ? customer : this.languageService.translate("Not found")
  }
  /**
   * router qua trang chi tiết bill
   * @example this.goToDetail('DMNWIDHNBIUWHGDUWY')
   */
  goToDetail(data) {
    let dataRestore = { searchValue: this.searchValue, filterType: this.filterType, startTime: this.date[0], endTime: this.date[1], invoice: this.invoices }
     this.router.navigate(["/sales/dashboard/manage/invoice/temporary-sales-invoice/detail"], { state: { id: data._id, dataRestore } })
   
  }
  public filterType = 'all'

  /**
   * lọc hóa đơn theo khách hàng
   * @example this.sortFilter('all')
   */
  sortFilter(filterType) {
    if (filterType == 'all') {
      this.invoiceFilter = this.invoices
    }
    else {
      this.invoiceFilter = this.invoices.filter(item => item.id_customer == filterType)
    }

    this.searchBill(this.searchValue)
  }
  /**
   * lọc bill theo ['bill_code', 'partner_name', 'payment_name', 'status', 'name_employee']
   * @example this.searchBill('')
   */
  searchBill(value: string) {
    if (value) {
      let val: string = this.vhAlgorithm.changeAlias(value.trim().toLowerCase())
      this.invoiceShow = this.vhAlgorithm.searchList(val, this.invoiceFilter, ['bill_code', 'partner_name', 'payment_name', 'name_employee', 'status'])
    } else this.invoiceShow = [...this.invoiceFilter]

    this.totalValInvoice = this.getTotal()
  }
  /**
   * trả về tổng daonh số của tất cả hóa đơn đang hiển thị
   * @returns 
   */
  getTotal() {
    return this.invoiceShow.reduce((prev: number, next) => prev + next.total * (1 + next.tax / 100), 0);
  }


  ngAfterViewChecked() {
    if (document.querySelector("#sales-return-invoice") && document.querySelector(".ant-table-thead") && document.querySelector(".invoice-header")) {
      this.tableHeight = document.querySelector("#sales-return-invoice").clientHeight - document.querySelector(".ant-table-thead").clientHeight
        - document.querySelector(".invoice-header").clientHeight - 95 + "px";
    }

    this.cdRef.detectChanges();
  }
  /**
   * trả về tên phương thức thanh toán của đơn
   * @example let payment_name= this.renderPayment(1)
   */
  renderPayment(payment_type: 1 | 2 | 3, id_wallet: string = null) {
    switch (payment_type) {
      case 1: return this.languageService.translate('Cash')
      case 2: return this.languageService.translate('Debit')
      case 3: return this.vhQuerySales.getlocalWallet(id_wallet).name
    }
  }
  goBack() {
    this.router.navigate(["/sales/dashboard/manage/invoice"])
  }

  /**
   * bắt sk thay đổi thời gian xem hóa đơn
   * @param event array<date>
   * @example this.changeTime([2023-06-23T07:12:35.157Z,2023-06-23T07:12:35.157Z])
   */
  changeTime(event) {
    if (new Date(event[1].setHours(0, 0, 0, 0)).getTime() - new Date(event[0].setHours(0, 0, 0, 0)).getTime() <= this.fncService.checkDateReport() * 86000000) {
      //this.getTransferList();
    } else {
      this.vhComponent.showToast(4000, this.languageService.translate("The time limit to view is") + " " + this.fncService.checkDateReport() + " " + this.languageService.translate("days"), "alert-toast")
      this.date = [new Date(event[0]), new Date(event[0].getTime() + this.fncService.checkDateReport() * 86000000)]
    }
  }

  /**
   * lưu mã đơn vào RAM
   * @example this.copyBillCode('230623A001')
   */
  copyBillCode(billCode) {
    this.vhComponent.copyValue(billCode)
  }

  /** hàm sort cho các cột */
  dateCol = false;
  bill_codeCol = false;
  partner_nameCol = false;
  payment_nameCol = false;
  name_employeeCol = false;
  sum_priceCol = false;
  /** Hàm thực hiện sắp xếp theo collection
   *
   * @param colName       // tên cột muôn sắp xếp
   */
  sortTable(colName) {
    switch (colName) {
      case 'date':
        if (this.dateCol) {
          this.invoiceShow = this.vhAlgorithm.sortDatebyASC([...this.invoiceShow], colName);
        } else {
          this.invoiceShow = this.vhAlgorithm.sortDatebyDESC([...this.invoiceShow], colName);
        }
        break;
      case 'bill_code':
        if (this.bill_codeCol) {
          this.invoiceShow = this.vhAlgorithm.sortVietnamesebyASC([...this.invoiceShow], colName);
        } else {
          this.invoiceShow = this.vhAlgorithm.sortVietnamesebyDESC([...this.invoiceShow], colName);
        }
        break;
      case 'partner_name':
        if (this.partner_nameCol) {
          this.invoiceShow = this.vhAlgorithm.sortVietnamesebyASC([...this.invoiceShow], colName);
        } else {
          this.invoiceShow = this.vhAlgorithm.sortVietnamesebyDESC([...this.invoiceShow], colName);
        }
        break;
      case 'payment_name':
        if (this.payment_nameCol) {
          this.invoiceShow = this.vhAlgorithm.sortVietnamesebyASC([...this.invoiceShow], colName);
        } else {
          this.invoiceShow = this.vhAlgorithm.sortVietnamesebyDESC([...this.invoiceShow], colName);
        }
        break;
      case 'name_employee':
        if (this.name_employeeCol) {
          this.invoiceShow = this.vhAlgorithm.sortVietnamesebyASC([...this.invoiceShow], colName);
        } else {
          this.invoiceShow = this.vhAlgorithm.sortVietnamesebyDESC([...this.invoiceShow], colName);
        }
        break;
      case 'sum_price':
        if (this.sum_priceCol) {
          this.invoiceShow = this.vhAlgorithm.sortNumberbyASC([...this.invoiceShow], colName);
        } else {
          this.invoiceShow = this.vhAlgorithm.sortNumberbyDESC([...this.invoiceShow], colName);
        }
        break;
    }
  }
  /**
   * lọc khách hàng theo tên
   * @param value 
   */
  searchFilter(value) {
    this.customers = this.vhAlgorithm.searchList(this.vhAlgorithm.changeAlias(value), this.vhQuerySales.getlocalCustomers().filter(item => item.type != 3), ['name'])
  }
}
