import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { TemporarySalesInvoiceComponent } from './temporary-sales-invoice.component';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { Routes, RouterModule } from '@angular/router';
import { IonicModule } from '@ionic/angular';
import { TranslateModule } from '@ngx-translate/core';
import { CanLeavePage } from 'src/app/canLeavePage';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { EditTemporaryComponent } from './edit-temporary/edit-temporary.component';
import { DetailComponent } from './detail/detail.component';
import { ScrollingModule } from '@angular/cdk/scrolling';
import { SalesDesktopBillType1Module } from 'src/app/sales/components/bill-type/bill-type-1/sales-desktop/sales-desktop-bill-type1.module';

const routes: Routes = [
  {
    path: '',
    children: [
      {
        path: "",
        component: TemporarySalesInvoiceComponent
      },
      {
        path: "detail",
        component: DetailComponent
      },
      {
        path: "edit-sales",
        component: EditTemporaryComponent,
       
      },
    ]
  },

]

@NgModule({
  declarations: [
    TemporarySalesInvoiceComponent,
    EditTemporaryComponent, DetailComponent, 
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    TranslateModule,
    IonicModule,
    FormsModule,
    ReactiveFormsModule,
    ScrollingModule,
    SalesDesktopBillType1Module
  ]
})
export class TemporarySalesInvoiceModule { }
